"use client";
import { useState } from "react";
import { createQuiz } from "@/actions/todo.action";

export default function CreateQuiz() {
  const [text, setText] = useState("");
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    const file = e.target[0].files[0];
    if (!file) return alert("Please upload a PDF file");

    try {
      setLoading(true);

      const formData = new FormData();
    formData.append("file", file);
  
    const res = await fetch("/api/parse-pdf", {
      method: "POST",
      body: formData,
    });
  
    const data = await res.json();
    const extractedText = data.text || "";

    const quizRes = await fetch("/api/gemini-route", {
      method: "POST",
      headers: { "Content-Type": "application/json" }, 
      body: JSON.stringify({ text: extractedText }),
    });
    
    const quizData = await quizRes.json();
    setText(quizData.text || ""); 

    await createQuiz(quizData.text);
    }
    catch (error){
      console.log(error);
    }
    finally {
      setLoading(false);
    }
  };  

  return (
    <div>
      <div>Create Todo</div>
      <form onSubmit={handleSubmit}>
        <input type="file" className="formInput" />
        <button className="formButton" disabled={loading}>
          {loading ? "Processing...": "Submit PDF"}
        </button>
      </form>
    </div>
  );
}

